<script>
    const GetCallInHistoyrUrl = "GetDataApi/GetCallinHistory"
    import CallinHistory from "../JsonData/CallinHistory"

    export default {
        data() {
            return {
                CallInHistory: {
                    item: [],
                    status: ''
                },
            }
        },
        watch: {
            $route(to, from) {
                this.getCallInHistory()

            },
        },
        mounted() {
            this.getCallInHistory()

        },
        computed: {
            Today: function () {
                const date = new Date()
                return date.toLocaleDateString()
            }
        },
        methods: {
            //取得通話歷史紀錄
            getCallInHistory() {
                let EndDate = new Date(new Date().setDate(new Date().getDate() + 1)).toLocaleDateString();
                let StartDate = new Date().toLocaleDateString();
                let myServicePhone = this.$route.params.ServicePhone
                this.CallInHistory.item = CallinHistory
            },
            imgError(item) {
                item.pictureUrl = require('../assets/DefaultPic.png')
            },
        }
    }
</script>
<style>
    #CallInHistoryv2 {
