            -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
            background-color: #23262b;
        }

        #CallInHistoryv2 .more {
            color: #0090FF;
            font-size: 18px;
        }

        #CallInHistoryv2 .CallInHistoryWrap {
            margin-top: 20px;
            height: calc(100vh - 581px);
            overflow-y: scroll;
        }

            #CallInHistoryv2 .CallInHistoryWrap .CallInHistoryItem {
                background: #1D1E24;
