    const RegistrationTopicURL = '/API/RegistrationTopic'

    // Optionally install plugin
    Vue.use(BootstrapVue)
    Vue.use(IconsPlugin)
    Vue.use(firestorePlugin)

    export default {
        name: 'app',
        data() {
            return {
                token: '',
                AccountID: '',
                isRouterAlive: true
            }
