        computed: {   //撈出 store.js 裡面的 state.status & state.MemberPhone
            ...mapState(['CTI'])
        },
       created() {
            let _this = this

        },
        watch: {
          
        },
        mounted() {
           
        },
        methods: {
            reload () {
                this.isRouterAlive = false
                this.$nextTick(() => (this.isRouterAlive = true))
            },   
          
        },
    }
</script>

